var _this
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcons from "@/components/uni-icons/uni-icons.vue"
import uniFav from '@/components/uni-fav/uni-fav.vue'
export default {
	comments:{
		uniNavBar,
		uniIcons,
		uniFav
	},
	data() {
		return {
			indicatorDots: true,
			current:0,
			list:[
				{key:"1",title:"A"},
				{key:"2",title:"B"},
				{key:"3",title:"C"},
				{key:"4",title:"D"},
				{key:"6",title:"E"},
				{key:"7",title:"F"}
			],
			likemessage:144,
			favmessage:219
		}
	},
	created() {
		_this = this,
		_this.getOrder()
	},
	
	methods: {
		change(e){
			_this.current = e.detail.current
		},
		getOrder(){
			_this.swiperlength,
			_this.list = [
				{key:"1",title:"A"},
				{key:"2",title:"B"},
				{key:"3",title:"C"},
				{key:"4",title:"D"},
				{key:"6",title:"E"},
				{key:"7",title:"F"}
			]
		},
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		home(){
			uni.switchTab({
				url:"/pages/index/index"
			})
		},
		likeclick(){
			// console.log("cccccccccccc")
			this.likemessage++
		},
		favclick(){
			// console.log("cccccccccccc")
			this.favmessage++
		}
		// favClick() {
		// 	this.checked = !this.checked
		//  this.$forceUpdate()
		// }
	}
}
